import React, { useState } from "react";
import { useSpring, animated } from "react-spring";
import VisibilitySensor from "react-visibility-sensor";
import { classNames } from "helpers/functions";
import styles from "./ChurchServices.module.scss";

const ServiceType = ({ service, aosDirection = "" }) => {
  const [flippedCard, setFlippedCard] = useState(false);

  const { transform, opacity } = useSpring({
    opacity: flippedCard ? 1 : 0,
    transform: `perspective(600px) rotateX(${flippedCard ? 180 : 0}deg)`,
    config: { mass: 5, tension: 500, friction: 80 },
  });

  const handleOnClickFLipCard = () => setFlippedCard(!flippedCard);

  return (
    <VisibilitySensor
      partialVisibility={"bottom"}
      offset={{
        bottom: -350,
      }}
    >
      {({ isVisible }) => {
        let aosClassName = "";
        if (isVisible)
          aosClassName = aosDirection === "left" ? styles.scrolledLeft : styles.scrolledRight;
        else
          aosClassName = aosDirection === "left" ? styles.scrolledLeftOut : styles.scrolledRightOut;

        return (
          <div className={classNames(aosClassName, styles.serviceTypeWrapper)}>
            <div className={styles.typeHeader}>
              <span className={styles.typeName}>{service.title}</span>
              <span>@ {service.time.from}</span>
            </div>
            <div className={styles.typeImage} onClick={handleOnClickFLipCard}>
              <animated.div
                className={styles.flipImage}
                style={{
                  opacity: opacity.interpolate((o) => 1 - o),
                  transform,
                  backgroundImage: `url(${service.images[0]})`,
                }}
              />
              <animated.div
                className={styles.flipImage}
                style={{
                  opacity,
                  transform: transform.interpolate((t) => `${t} rotateX(180deg)`),
                  backgroundImage: `url(${service.images[1]})`,
                }}
              />
            </div>
          </div>
        );
      }}
    </VisibilitySensor>
  );
};

export default ServiceType;
