import { ServicesListType, ServicesListTypeI18n } from "ServicesListType";
import { FirebaseDocumentType } from "FirebaseDataType";
import { firestoreParser } from "helpers/functions";
import { firebaseNameDirectory } from "constants/common";

export function fetchServicesList(): Promise<Array<ServicesListType>> {
  const lang = sessionStorage.getItem("lang");

  return new Promise((resolve, reject) => {
    fetch(
      `https://firestore.googleapis.com/v1/projects/tlcfm-japan/databases/(default)/documents/servicesList`
    )
      .then(async (res) => {
        const data: FirebaseDocumentType = await res.json();
        const parsedData = firestoreParser(data)
          .documents.map((item: FirebaseDocumentType) => {
            const fields: ServicesListTypeI18n = item.fields as ServicesListTypeI18n;
            return {
              ...fields,
              id: item.name.replace(firebaseNameDirectory + "servicesList/", ""),
              title: fields.title[lang as "en" | "ja"],
            };
          })
          .sort((a: any, b: any) => a.order - b.order);

        resolve(parsedData);
      })
      .catch((data) => reject(data));
  });
}
