import { MinistriesType } from "MinistriesType";
import { combineReducers } from "redux";
import { createReducer } from "typesafe-actions";
import { fetchMinistriesAsync } from "store/actions/ministries";

export const isFetching = createReducer(false as boolean)
  .handleAction([fetchMinistriesAsync.request], (_state, _action) => true)
  .handleAction(
    [fetchMinistriesAsync.success, fetchMinistriesAsync.failure],
    (_state, _action) => false
  );

export const payload = createReducer([] as Array<MinistriesType>).handleAction(
  fetchMinistriesAsync.success,
  (_state, action) => action.payload
);

const ministriesReducer = combineReducers({
  isFetching,
  payload,
});

export default ministriesReducer;
export type MinistriesState = ReturnType<typeof ministriesReducer>;
