import React, { useEffect } from "react";
// import { useHistory } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useSelector, useDispatch } from "react-redux";
import VisibilitySensor from "react-visibility-sensor";
// import { Button } from "@material-ui/core";
// HELPERS
import { classNames } from "helpers/functions";
// REDUX
import { fetchMinistriesAsync } from "store/actions/ministries";
import { homeDetails } from "store/selectors/homeDetails";
import { ministries } from "store/selectors/ministries";
// STYLES
import styles from "./ChurchMinistries.module.scss";

const ChurchMinistries: React.FC = () => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  // const history = useHistory();
  const homePayload = useSelector(homeDetails);
  const { payload } = useSelector(ministries);

  useEffect(() => {
    dispatch(fetchMinistriesAsync.request());
  }, [dispatch]);

  // const handleOnClickSeeMore = (ministryId: string) => () =>
  //   history.push({
  //     pathname: `/ministryDetails/${ministryId}`,
  //   });

  return (
    <div
      id={"churchMinistries"}
      className={styles.wrapper}
      style={{
        background: `url(${homePayload.payload.ministriesBG}) center center / cover no-repeat`,
      }}
    >
      <div className={styles.container}>
        <p className={styles.sectionTitle}>{t("ministries.title")}</p>
        <div className={styles.ministryList}>
          {payload.length &&
            payload.map((item) => {
              return (
                <VisibilitySensor
                  key={item.id}
                  partialVisibility={"bottom" as any}
                  offset={{ bottom: -650 }}
                >
                  {({ isVisible }) => (
                    <div
                      className={classNames(styles.ministryItem, isVisible && styles.scrolledUp)}
                    >
                      <div className={styles.summaryContainer}>
                        <span className={styles.ministryName}>{item.name}</span>
                        <span className={styles.summary}>{item.ministrySummary}</span>
                      </div>
                      <img className={styles.banner} src={item.banner} alt={item.name} />
                      {/* Uncomment when implementing the ministry details */}
                      {/* <Button
                    className={styles.seeMore}
                    variant="outlined"
                    onClick={handleOnClickSeeMore(item.id)}
                  >
                    {t("ministries.seeMore")}
                  </Button> */}
                    </div>
                  )}
                </VisibilitySensor>
              );
            })}
        </div>
      </div>
    </div>
  );
};

export default ChurchMinistries;
