import React from "react";
import { Route, BrowserRouter as Router } from "react-router-dom";
import App from "_root/App";
import Home from "pages/Home";
import EventList from "pages/EventList";
import EventDetails from "pages/EventDetails";
import LifeGroups from "pages/LifeGroups";
import MinistryDetails from "pages/MinistryDetails";

const routing = (
  <Router>
    <App>
      <Route exact path="/" component={Home} />
      <Route exact path="/events" component={EventList} />
      <Route exact path="/events/:eventId" component={EventDetails} />
      <Route exact path="/lifeGroups" component={LifeGroups} />
      <Route exact path="/ministryDetails/:ministryId" component={MinistryDetails} />
    </App>
  </Router>
);

export default routing;
