import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import { Helmet } from "react-helmet";
// COMPONENTS
import { Spinner } from "components";
// CHILDREN COMPONENTS
import Banner from "./components/Banner";
import ChurchServices from "./components/ChurchServices";
import ChurchLifeGroups from "./components/ChurchLifeGroups";
import ChurchMinistries from "./components/ChurchMinsitries";
import ChurchEvents from "./components/ChurchEvents";
import MeetOurPastor from "./components/MeetOurPastor";
import ChurchLocation from "./components/ChurchLocation";
import ContactUs from "./components/ContactUs";
// REDUX
import { fetchHomeDetailsAsync } from "store/actions/homeDetails";
import { homeDetails } from "store/selectors/homeDetails";
// STYLES
import styles from "./Home.module.scss";

const Home: React.FC = () => {
  const { i18n } = useTranslation();
  const dispatch = useDispatch();
  const { isFetching } = useSelector(homeDetails);

  useEffect(() => {
    window.scroll({
      top: 0,
      left: 0,
      behavior: "smooth",
    });
    return () => {};
  }, []);

  useEffect(() => {
    dispatch(fetchHomeDetailsAsync.request());
  }, [dispatch, i18n.language]);

  return isFetching ? (
    <Spinner className={styles.spinner} />
  ) : (
    <>
      <Helmet>
        <title>{`Tokyo Lighthouse Church Filipino Ministry`}</title>
      </Helmet>
      <Banner />
      <ChurchServices />
      <ChurchLifeGroups />
      <ChurchMinistries />
      <ChurchEvents />
      <MeetOurPastor />
      <ChurchLocation />
      <ContactUs />
    </>
  );
};

export default Home;
