import React from "react";
import { useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import VisibilitySensor from "react-visibility-sensor";
// HELPERS
import { classNames } from "helpers/functions";
// REDUX
import { homeDetails } from "store/selectors/homeDetails";
// STYLES
import styles from "./MeetOurPastor.module.scss";

const MeetOurPastor: React.FC = () => {
  const { t } = useTranslation();
  const { payload } = useSelector(homeDetails);

  return (
    <div className={styles.wrapper}>
      <span className={styles.sectionTitle}>{t("meetPastor.title")}</span>
      <span className={styles.pastorName}>{t("meetPastor.pastorName")}</span>
      <div className={styles.detailsContainer}>
        <VisibilitySensor partialVisibility={"bottom" as any} offset={{ bottom: -150 }}>
          {({ isVisible }) => (
            <div
              className={classNames(
                isVisible ? styles.scrolledLeft : styles.scrolledLeftOut,
                styles.pastorImg
              )}
              style={{ background: `url(${payload.pastorImg}) center center / contain no-repeat` }}
            />
          )}
        </VisibilitySensor>
        <VisibilitySensor partialVisibility={"bottom" as any} offset={{ bottom: -350 }}>
          {({ isVisible }) => (
            <div
              className={classNames(
                isVisible ? styles.scrolledRight : styles.scrolledRightOut,
                styles.pastorMsg
              )}
            >
              {payload.pastorMsgArr?.length &&
                payload.pastorMsgArr.map((item, index) => <span key={index}>{item}</span>)}
            </div>
          )}
        </VisibilitySensor>
      </div>
    </div>
  );
};

export default MeetOurPastor;
