import { EventDetailsType } from "EventDetailsType";
import { combineReducers } from "redux";
import { createReducer } from "typesafe-actions";
import { fetchEventDetailsAsync } from "store/actions/eventDetails";

export const isFetching = createReducer(false as boolean)
  .handleAction([fetchEventDetailsAsync.request], (_state, _action) => true)
  .handleAction(
    [fetchEventDetailsAsync.success, fetchEventDetailsAsync.failure],
    (_state, _action) => false
  );

export const payload = createReducer({} as EventDetailsType).handleAction(
  fetchEventDetailsAsync.success,
  (_state, action) => action.payload
);

const eventDetailsReducer = combineReducers({
  isFetching,
  payload,
});

export default eventDetailsReducer;
export type EventDetailsState = ReturnType<typeof eventDetailsReducer>;
