import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import { Helmet } from "react-helmet";
// ROUTER
import { useParams, RouteComponentProps } from "react-router-dom";
import { StaticContext } from "react-router";
// UI FRAMEWORK
import { AccessTime, Facebook, LocationOn } from "@material-ui/icons";
import { Link } from "@material-ui/core";
// COMPONENT
import Spinner from "components/Spinner";
// HELPERS
import { classNames, getDateFormat } from "helpers/functions";
// REDUX
import { eventDetails } from "store/selectors/eventDetails";
import { fetchEventDetailsAsync } from "store/actions/eventDetails";
// STYLES
import styles from "./EventDetails.module.scss";

type UrlParams = {
  eventId: string;
};

type LocationState = {
  isPastEvents?: boolean;
};

type Props = RouteComponentProps<{}, StaticContext, LocationState>;

const EventDetails: React.FC<Props> = ({ location, history }) => {
  const urlParams: UrlParams = useParams();
  const dispatch = useDispatch();
  const { t, i18n } = useTranslation();
  const { payload, isFetching } = useSelector(eventDetails);

  useEffect(() => {
    window.scroll({
      top: 0,
      left: 0,
    });
    dispatch(fetchEventDetailsAsync.request(urlParams.eventId));
  }, [dispatch, urlParams, i18n.language]);

  const handleOnClickBack = () => {
    history.push({
      pathname: "/events",
      state: {
        isPastEvents: location.state?.isPastEvents,
      },
    });
  };

  return (
    <div className={classNames(styles.wrapper, isFetching ? styles.fetchingItems : "")}>
      <Helmet>
        <meta property="og:type" content="website" />
        <meta property="fb:app_id" content="269043787717320" />
        <meta property="og:description" content="Come and join us for this event." />
        <meta property="og:url" content={`https://tlcfm.herokuapp.com${location.pathname}`} />
        <meta property="og:title" content={payload.name} />
        <meta property="og:image" content={payload.bannerImg} />
        <title>{`TLC-FM ${payload.name}`}</title>
      </Helmet>
      {isFetching ? (
        <Spinner />
      ) : (
        Object.keys(payload).length !== 0 && (
          <>
            <div className={styles.eventTitleWrapper}>
              <div className={styles.backBtn} onClick={handleOnClickBack}>
                <span></span>
                <span></span>
                Back
              </div>
              <div>
                <img
                  className={styles.eventBanner}
                  src={payload.bannerImg}
                  alt={payload.location.name}
                />
                <div className={styles.eventTitle}>
                  <span className={classNames(styles.marginBtm10)}>
                    {getDateFormat(payload.date, true)}
                  </span>
                  <h1 className={classNames(styles.eventName)}>{payload.name}</h1>
                  <div
                    className={"fb-share-button"}
                    data-href={`https://tlcfm.herokuapp.com${location.pathname}`}
                    data-layout="button"
                    data-size="large"
                  >
                    <Link
                      className={styles.fbLink}
                      href={`https://www.facebook.com/sharer/sharer.php?u=https://tlcfm.herokuapp.com${location.pathname}&amp;src=sdkpreparse`}
                      target={"_blank"}
                      rel={"noopener noreferrer"}
                    >
                      <Facebook /> <span>{t("eventDetails.share")}</span>
                    </Link>
                  </div>
                </div>
              </div>
            </div>
            <div className={styles.eventDetailsWrapper}>
              <div className={styles.eventTimeLocationWrapper}>
                <div className={styles.eventTimeLocation}>
                  <div className={styles.eventTimeWrapper}>
                    <AccessTime className={styles.icon} />
                    <div className={styles.eventTime}>
                      <span>{getDateFormat(payload.date, true)}</span>
                      {payload.time ? (
                        <span>{`${payload.time.from} - ${payload.time.to}`}</span>
                      ) : (
                        t("eventDetails.allDayEvent")
                      )}
                    </div>
                  </div>
                  <div className={styles.eventLocationWrapper}>
                    <LocationOn className={styles.icon} />
                    <div className={styles.eventLocation}>
                      <span className={styles.marginBtm10}>{payload.location.name}</span>
                      <span className={styles.eventAddress}>{payload.location.address}</span>
                      <Link
                        href={payload.location.googleMapLink}
                        target={"_blank"}
                        rel={"noopener"}
                      >
                        {t("eventDetails.clickHereForDirection")}
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
              {/* To be implemented once finalized the design and content */}
              {/* <div className={styles.eventDescription}>{payload.details}</div> */}
            </div>
          </>
        )
      )}
    </div>
  );
};

export default EventDetails;
