import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
// ROUTER
import { useHistory } from "react-router-dom";
// UI FRAMEWORK
import { Button } from "@material-ui/core";
// TYPES
import { EventListType } from "EventListType";
// COMPONENTS
import EventSummary from "components/EventSummary";
// HELPERS
import { useDeviceType } from "helpers/custom hooks";
//  REDUX
import { eventList } from "store/selectors/eventList";
import { fetchEventListAsync } from "store/actions/eventList";
import { homeDetails } from "store/selectors/homeDetails";
// STYLES
import styles from "./ChurchEvents.module.scss";

const ChurchEvents = () => {
  const { t } = useTranslation();
  const history = useHistory();
  const dispatch = useDispatch();
  const deviceType = useDeviceType();
  const { payload } = useSelector(eventList);
  const homePayload = useSelector(homeDetails);
  const upcomingEvents = payload.upcomingEvents;

  useEffect(() => {
    dispatch(fetchEventListAsync.request());
  }, [dispatch]);

  const handleOnClickViewMore = () => history.push("/events");

  return (
    <div
      id={"upcomingEvents"}
      className={styles.wrapper}
      style={{
        background: `url(${homePayload.payload.eventsBG}) center center / cover no-repeat`,
        // Parallex is not supported in tl and sp
        backgroundAttachment: deviceType === "pc" ? "fixed" : "initial",
      }}
    >
      <div className={styles.container}>
        <p className={styles.sectionTitle}>{t("events.title")}</p>
        <div className={styles.eventListContainer}>
          {upcomingEvents && upcomingEvents.length ? (
            upcomingEvents.map((item: EventListType) => {
              return <EventSummary key={item.id} details={item} />;
            })
          ) : (
            <p className={styles.noUpcomingEvents}>{t("events.noUpcomingEvents")}</p>
          )}
        </div>
        <Button className={styles.viewMore} variant="outlined" onClick={handleOnClickViewMore}>
          {t("events.moreEvents")}
        </Button>
      </div>
    </div>
  );
};

export default ChurchEvents;
