import dayjs from "dayjs";

type EventDate = {
  from: string;
  to?: string;
};

const getDateFormat = (eventDate: EventDate, isWithWeekName: boolean = false) => {
  const fromDate = dayjs(eventDate.from);
  let newEventDate = fromDate.format("MMM. DD, YYYY");
  const curYear = new Date().getFullYear().toString();
  const weekName = isWithWeekName ? `${dayjs(fromDate).format("dddd")}, ` : "";

  if (eventDate.to) {
    const toDate = dayjs(eventDate.to);
    newEventDate = `${fromDate.format("MMM. DD, YYYY")} - ${toDate.format("MMM. DD, YYYY")}`;

    if (
      toDate.format("M") === fromDate.format("M") &&
      toDate.format("YY") === fromDate.format("YY")
    ) {
      newEventDate = `${fromDate.format("MMM. DD")} - ${toDate.format("DD")}, ${
        curYear !== fromDate.format("YYYY") ? fromDate.format("YYYY") : curYear
      }
      `;
    } else if (toDate.format("YY") === fromDate.format("YY")) {
      newEventDate = `${fromDate.format("MMM. DD")} - ${toDate.format("MMM. DD")}, ${
        curYear !== fromDate.format("YYYY") ? fromDate.format("YYYY") : curYear
      }
      `;
    }
  }

  return weekName + newEventDate.trim();
};

export default getDateFormat;
