import { Epic } from "redux-observable";
import { from, of } from "rxjs";
import { filter, mergeMap, map, catchError } from "rxjs/operators";
import { RootAction, RootState, Services, isActionOf } from "typesafe-actions";

import { fetchMinistriesAsync } from "store/actions/ministries";

export const fetchMinistriesAsyncEpic: Epic<RootAction, RootAction, RootState, Services> = (
  action$,
  _state$,
  { api }
) =>
  action$.pipe(
    filter(isActionOf(fetchMinistriesAsync.request)),
    mergeMap(() =>
      from(api.ministries.fetchMinistries()).pipe(
        map(fetchMinistriesAsync.success),
        catchError((message: string) => of(fetchMinistriesAsync.failure(message)))
      )
    )
  );
