import React from "react";
import { useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
// ROUTER
import { useHistory } from "react-router-dom";
//  UI FRAMEWORK
import { Button } from "@material-ui/core";
// HELPERS
import { useDeviceType } from "helpers/custom hooks";
// REDUX
import { homeDetails } from "store/selectors/homeDetails";
// STYLES
import styles from "./ChurchLifeGroups.module.scss";

const ChurchLifeGroups: React.FC = () => {
  const { t } = useTranslation();
  const history = useHistory();
  const { payload } = useSelector(homeDetails);
  const deviceType = useDeviceType();

  const handleOnClickView = () => history.push("/lifeGroups");

  return (
    <div
      id={"churchLifeGroups"}
      className={styles.wrapper}
      style={{
        background: `url(${payload.lifeGroupsBG}) center center / cover no-repeat`,
        // Parallex is not supported in tl and sp
        backgroundAttachment: deviceType === "pc" ? "fixed" : "initial",
      }}
    >
      <div className={styles.container}>
        <p className={styles.sectionTitle}>{t("lifeGroups.title")}</p>
        <p className={styles.description}>{t("lifeGroups.description")}</p>
        <Button className={styles.joinLifeGroup} variant="outlined" onClick={handleOnClickView}>
          {t("lifeGroups.joinNow")}
        </Button>
      </div>
    </div>
  );
};

export default ChurchLifeGroups;
