import React from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
// HELPERS
import { useDeviceType } from "helpers/custom hooks";
import { classNames } from "helpers/functions";
// REDUX
import { homeDetails } from "store/selectors/homeDetails";
// STYLES
import styles from "./Banner.module.scss";

const Banner: React.FC = () => {
  const { t } = useTranslation();
  const deviceType = useDeviceType();
  const { payload } = useSelector(homeDetails);

  return (
    <>
      <div className={styles.wrapper}>
        <div className={styles.bannerWrapper}>
          <div
            className={styles.bannerImage}
            style={{
              background: `url(${payload.bannerBG}) center center / cover no-repeat`,
              // Parallex is not supported in tl and sp
              backgroundAttachment: deviceType === "pc" ? "fixed" : "initial",
            }}
          />
          <div className={styles.bannerText}>
            <span className={styles.welcome}>{t("banner.welcome")}</span>
            <div className={styles.churchName}>
              <span className={classNames(styles.scrolledLeft, styles.tlc)}>
                {t("banner.churchName")}
              </span>
              <span className={classNames(styles.scrolledRight, styles.tlc)}>
                {t("banner.ministryName")}
              </span>
            </div>
            <span className={styles.joinus}>
              <span>{payload.bannerMsg}</span>
              <span>@ {payload.bannerTime}</span>
            </span>
          </div>
        </div>
      </div>
    </>
  );
};

export default Banner;
