import { HomeDetailsType, HomeDetailsTypeI18n } from "HomeDetailsType";
import { FirebaseDocumentType } from "FirebaseDataType";
import { firestoreParser } from "helpers/functions";
import { firebaseNameDirectory } from "constants/common";

export function fetchHomeDetails(): Promise<HomeDetailsType> {
  const lang = sessionStorage.getItem("lang");

  return new Promise((resolve, reject) => {
    fetch(
      `https://firestore.googleapis.com/v1/projects/tlcfm-japan/databases/(default)/documents/homeDetails`
    )
      .then(async (res) => {
        const data: FirebaseDocumentType = await res.json();
        const parsedData = firestoreParser(data).documents.map((item: FirebaseDocumentType) => {
          const fields: HomeDetailsTypeI18n = item.fields as HomeDetailsTypeI18n;
          const newPastorMsgArr: Array<string> = [];
          fields.pastorMsgArr.forEach((item) => {
            newPastorMsgArr.push(item[lang as "en" | "ja"]);
          });

          return {
            ...fields,
            id: item.name.replace(firebaseNameDirectory + "homeDetails/", ""),
            pastorMsgArr: newPastorMsgArr,
            bannerMsg: fields.bannerMsg[lang as "en" | "ja"],
          };
        })[0];

        resolve(parsedData);
      })
      .catch((data) => reject(data));
  });
}
