import React from "react";
import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";
import { HashLink as Link } from "react-router-hash-link";
import { Button } from "@material-ui/core";
import classNames from "helpers/functions/classNames";
import { useDeviceType } from "helpers/custom hooks";
import styles from "./Header.module.scss";

type Props = {
  isScrolled?: boolean;
  onClick?: () => void;
};

const HeaderLinks: React.FC<Props> = ({ isScrolled = false, onClick }) => {
  const { t } = useTranslation();
  const location = useLocation();
  const deviceType = useDeviceType();
  const isEventsPage = location.pathname.includes("/events");
  const isMinsitryDetailPage = location.pathname.includes("/ministryDetails");
  const hideOtherBtn =
    ["/events", "/lifeGroups"].includes(location.pathname) || isEventsPage || isMinsitryDetailPage;

  return (
    <>
      <Button
        className={classNames(
          styles.btnFormat,
          (isScrolled || (isEventsPage && deviceType === "pc")) && styles.textBlack
        )}
      >
        <Link smooth to="/" onClick={onClick}>
          {t("header.home")}
        </Link>
      </Button>
      <Button
        className={classNames(
          styles.btnFormat,
          hideOtherBtn && styles.hideBtn,
          isScrolled && styles.textBlack
        )}
      >
        <Link smooth to="#churchServices" onClick={onClick}>
          {t("header.services")}
        </Link>
      </Button>
      <Button
        className={classNames(
          location.pathname === "/lifeGroups" && styles.btnSelected,
          styles.btnFormat,
          isMinsitryDetailPage && styles.hideBtn,
          (isScrolled || (isEventsPage && deviceType === "pc")) &&
            location.pathname !== "/lifeGroups" &&
            styles.textBlack
        )}
      >
        <Link
          smooth
          to={hideOtherBtn ? "/lifeGroups" : "#churchLifeGroups"}
          onClick={onClick}
          scroll={(el) => el.scrollIntoView({ behavior: "smooth", block: "center" })}
        >
          {t("header.lifeGroups")}
        </Link>
      </Button>
      <Button
        className={classNames(
          location.pathname === "/ministries" && styles.btnSelected,
          styles.btnFormat,
          hideOtherBtn && styles.hideBtn,
          (isScrolled || (isEventsPage && deviceType === "pc")) &&
            location.pathname !== "/ministries" &&
            styles.textBlack
        )}
      >
        <Link
          smooth
          to={hideOtherBtn ? "/ministries" : "#churchMinistries"}
          onClick={onClick}
        >
          {t("header.ministries")}
        </Link>
      </Button>
      <Button
        className={classNames(
          isEventsPage && styles.btnSelected,
          styles.btnFormat,
          isScrolled && !isEventsPage && styles.textBlack,
          isMinsitryDetailPage && styles.hideBtn
        )}
      >
        <Link
          smooth
          to={hideOtherBtn ? "/events" : "#upcomingEvents"}
          onClick={onClick}
          scroll={(el) => el.scrollIntoView({ behavior: "smooth", block: "center" })}
        >
          {t("header.events")}
        </Link>
      </Button>
      <Button
        className={classNames(
          styles.btnFormat,
          hideOtherBtn && styles.hideBtn,
          isScrolled && styles.textBlack
        )}
      >
        <Link
          smooth
          to="#churchLocation"
          onClick={onClick}
          scroll={(el) => el.scrollIntoView({ behavior: "smooth", block: "center" })}
        >
          {t("header.location")}
        </Link>
      </Button>
      <Button
        className={classNames(
          styles.btnFormat,
          hideOtherBtn && styles.hideBtn,
          isScrolled && styles.textBlack
        )}
      >
        <Link smooth to="#contactus" onClick={onClick}>
          {t("header.contactUs")}
        </Link>
      </Button>
    </>
  );
};

export default HeaderLinks;
