import { combineEpics } from "redux-observable";

import * as eventDetailsEpics from "store/epics/eventDetails";
import * as eventListEpic from "store/epics/eventList";
import * as servicesListEpic from "store/epics/servicesList";
import * as homeDetailsEpic from "store/epics/homeDetails";
import * as lifeGroupListEpic from "store/epics/lifeGroupList";
import * as ministriesEpic from "store/epics/ministries";

export default combineEpics(
  ...Object.values(eventDetailsEpics),
  ...Object.values(eventListEpic),
  ...Object.values(servicesListEpic),
  ...Object.values(homeDetailsEpic),
  ...Object.values(lifeGroupListEpic),
  ...Object.values(ministriesEpic)
);
