import React, { FormEvent, useRef, useState, ChangeEvent } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
// UI FRAMEWORK
import { Button, TextField, Snackbar } from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";
// HELPERS
import { useDeviceType } from "helpers/custom hooks";
// REDUX
import { homeDetails } from "store/selectors/homeDetails";
// STYLES
import styles from "./ContactUs.module.scss";

// Firebase
import * as firebase from "firebase/app";

const formValid = {
  name: true,
  email: true,
  message: true,
};

const ContactUs: React.FC = () => {
  const { t } = useTranslation();
  const { payload } = useSelector(homeDetails);
  const deviceType = useDeviceType();
  const formRef = useRef(null);
  const [formValidation, setFormValidation] = useState(formValid);
  const [snackBarOpen, setSnackBarOpen] = useState(false);

  const handleOnChangeField = (e: ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
    const field: EventTarget & (HTMLInputElement | HTMLTextAreaElement) = e.currentTarget;
    const isFieldUpdated =
      ((field.name === "name" && !formValidation.name) ||
        (field.name === "email" && !formValidation.email) ||
        (field.name === "message" && !formValidation.message)) &&
      !!field.value;

    if (isFieldUpdated) {
      const checkFormValidation = {
        name: field.name === "name" ? !!field.value : formValidation.name,
        email: field.name === "email" ? !!field.value : formValidation.email,
        message: field.name === "message" ? !!field.value : formValidation.message,
      };
      setFormValidation(checkFormValidation);
    }
  };

  const handleSubmitForm = (e: FormEvent) => {
    e.preventDefault();
    const form: any = formRef.current;
    const checkFormValidation = {
      name: !!form["name"].value,
      email: !!form["email"].value,
      message: !!form["message"].value,
    };
    setFormValidation(checkFormValidation);
    if ((!!form["name"].value && !!form["email"].value) || !!form["message"].value) {
      firebase
        .firestore()
        .collection("contactUsMessageList")
        .add({
          name: form["name"].value,
          email: form["email"].value,
          message: form["message"].value,
          timestamp: firebase.firestore.FieldValue.serverTimestamp(),
        })
        .then(() => setSnackBarOpen(true))
        .catch((error) => {
          return console.error("Error writing message to database", error);
        });

      form["name"].value = "";
      form["email"].value = "";
      form["message"].value = "";
    }
  };

  const handleCloseSnackBar = (event?: React.SyntheticEvent, reason?: string) => {
    if (reason === "clickaway") return;
    setSnackBarOpen(false);
  };

  return (
    <div
      id={"contactus"}
      className={styles.wrapper}
      style={{
        background: `url(${payload.contactUsBG}) center center / cover no-repeat`,
        // Parallex is not supported in tl and sp
        backgroundAttachment: deviceType === "pc" ? "fixed" : "initial",
      }}
    >
      <span className={styles.title}>{t("contactUs.title")}</span>
      <span>{t("contactUs.subTitle")}</span>
      <form ref={formRef} onSubmit={handleSubmitForm}>
        <div className={styles.formWrapper}>
          <TextField
            name={"name"}
            className={styles.textField}
            label={`*${t("contactUs.name")}`}
            placeholder={"Juan Dela Cruz"}
            variant="outlined"
            onChange={handleOnChangeField}
            error={!formValidation.name}
          />
          {!formValidation.name && (
            <span className={styles.errorMessage}>*{t("contactUs.requiredField")}</span>
          )}
          <TextField
            name={"email"}
            type={"email"}
            className={styles.textField}
            label={`*${t("contactUs.email")}`}
            variant="outlined"
            placeholder={"juan@delacruz.com"}
            onChange={handleOnChangeField}
            error={!formValidation.email}
          />
          {!formValidation.email && (
            <span className={styles.errorMessage}>*{t("contactUs.requiredField")}</span>
          )}
          <TextField
            name={"message"}
            className={styles.textField}
            label={`*${t("contactUs.message")}`}
            multiline
            rows={5}
            variant="outlined"
            placeholder={"Type your message here..."}
            onChange={handleOnChangeField}
            error={!formValidation.message}
          />
          {!formValidation.message && (
            <span className={styles.errorMessage}>*{t("contactUs.requiredField")}</span>
          )}
          <div className={styles.btnContainer}>
            <Button type={"submit"} className={styles.btnSend} variant="outlined">
              {t("contactUs.send")}
            </Button>
          </div>
        </div>
      </form>
      <Snackbar
        anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
        open={snackBarOpen}
        autoHideDuration={5000}
        onClose={handleCloseSnackBar}
      >
        <Alert elevation={6} variant="filled" onClose={handleCloseSnackBar} severity="success">
          {t("contactUs.messageSent")}
        </Alert>
      </Snackbar>
    </div>
  );
};

export default ContactUs;
