import React, { useEffect, SyntheticEvent } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import { Helmet } from "react-helmet";
// ROUTER
import { RouteComponentProps } from "react-router-dom";
import { StaticContext } from "react-router";
// UI FRAMEWORK
import { EventBusy } from "@material-ui/icons";
// TYPE
import { EventListType } from "EventListType";
// COMPONENTS
import { EventSummary, Spinner } from "components";
// HELPERS
import { classNames } from "helpers/functions";
// REDUX
import { fetchEventListAsync } from "store/actions/eventList";
import { eventList } from "store/selectors/eventList";
// STYLES
import styles from "./EventList.module.scss";

type LocationState = {
  isPastEvents?: boolean;
};

type Props = RouteComponentProps<{}, StaticContext, LocationState>;

const EventList: React.FC<Props> = ({ location, history }) => {
  const { t, i18n } = useTranslation();
  const dispatch = useDispatch();
  const { payload, isFetching } = useSelector(eventList);
  const eventListPayload = !location.state?.isPastEvents
    ? payload.upcomingEvents
    : payload.pastEvents;
  const yearList = Array.from(new Set(eventListPayload?.map((a) => a.year)));

  useEffect(() => {
    window.scroll({
      top: 0,
      left: 0,
    });
    dispatch(fetchEventListAsync.request());
  }, [dispatch, i18n.language]);

  const handleOnClickEventTypes = (e: SyntheticEvent) => {
    history.push({
      pathname: "/events",
      state: {
        isPastEvents: (e.target as HTMLSpanElement).id === "past",
      },
    });
  };

  return (
    <div className={styles.wrapper}>
      <Helmet>
        <title>{`TLC-FM Event List`}</title>
      </Helmet>
      {isFetching ? (
        <Spinner />
      ) : (
        <>
          <div className={styles.eventTypesContainer}>
            <span
              id={"upcoming"}
              className={classNames(
                !location.state?.isPastEvents && styles.selected,
                styles.eventType
              )}
              onClick={handleOnClickEventTypes}
            >
              {t("eventList.upcomingEvents")}
            </span>
            <span
              id={"past"}
              className={classNames(
                !!location.state?.isPastEvents && styles.selected,
                styles.eventType
              )}
              onClick={handleOnClickEventTypes}
            >
              {t("eventList.pastEvents")}
            </span>
          </div>
          <div
            className={classNames(
              styles.eventSummaryContainer,
              yearList.length ? styles.hasEventContainer : styles.noEventContainer
            )}
          >
            {yearList.length ? (
              yearList.map((item) => {
                const perYearEvents = eventListPayload.filter(
                  (eventItem: EventListType) => eventItem.year === item
                );
                const eventDisplay = perYearEvents.map((event: EventListType) => (
                  <EventSummary
                    key={event.id}
                    details={event}
                    isPastEvents={location.state?.isPastEvents}
                  />
                ));
                return (
                  <div key={item}>
                    <div className={styles.yearTitle}>{item}</div>
                    <div className={styles.eventPerYear}>{eventDisplay}</div>
                  </div>
                );
              })
            ) : (
              <div className={styles.noUpcomingEvents}>
                <EventBusy />
                {!location.state?.isPastEvents
                  ? t("eventList.noUpcomingEvents")
                  : t("eventList.noPastEvents")}
              </div>
            )}
          </div>
        </>
      )}
    </div>
  );
};

export default EventList;
