import { MinistriesType, MinistriesTypeI18n } from "MinistriesType";
import { FirebaseDocumentType } from "FirebaseDataType";
import { firestoreParser } from "helpers/functions";
import { firebaseNameDirectory } from "constants/common";

export function fetchMinistries(): Promise<Array<MinistriesType>> {
  const lang = sessionStorage.getItem("lang");

  return new Promise((resolve, reject) => {
    fetch(
      `https://firestore.googleapis.com/v1/projects/tlcfm-japan/databases/(default)/documents/ministries`
    )
      .then(async (res) => {
        const data: FirebaseDocumentType = await res.json();
        const parsedData = firestoreParser(data).documents.map((item: FirebaseDocumentType) => {
          const fields: MinistriesTypeI18n = item.fields as MinistriesTypeI18n;
          return {
            ...fields,
            id: item.name.replace(firebaseNameDirectory + "ministries/", ""),
            name: fields.name[lang as "en" | "ja"],
            ministrySummary: fields.ministrySummary[lang as "en" | "ja"],
          };
        });

        resolve(parsedData);
      })
      .catch((data) => reject(data));
  });
}
