import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useTranslation } from "react-i18next";
// UI FRAMEWORK
import { Link } from "@material-ui/core";
import FacebookIcon from "@material-ui/icons/Facebook";
// TYPE
import ServiceType from "./serviceType";
// COMPONENTS
import { Spinner } from "components";
// CONSTANTS
import { globalLink } from "constants/common";
// REDUX
import { fetchServicesListAsync } from "store/actions/servicesList";
import { servicesList } from "store/selectors/servicesList";
// STYLES
import styles from "./ChurchServices.module.scss";

const ChurchServices: React.FC = () => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const { payload, isFetching } = useSelector(servicesList);

  useEffect(() => {
    dispatch(fetchServicesListAsync.request());
  }, [dispatch]);

  return (
    <div id={"churchServices"} className={styles.wrapper}>
      <p className={styles.sectionTitle}>{t("services.joinUs")}</p>
      {isFetching ? (
        <Spinner />
      ) : (
        <>
          <div className={styles.schedules}>
            {payload?.map((item, index) => {
              const direction = index % 2 === 0 ? "left" : "right";
              return <ServiceType key={item.id} aosDirection={direction} service={item} />;
            })}
          </div>
          <div className={styles.liveStream}>
            <span className={styles.liveStreamText}>{t("services.liveStreaming")}:</span>
            <Link
              className={styles.fbLink}
              href={globalLink.fbLink}
              target={"_blank"}
              rel={"noopener"}
            >
              <FacebookIcon /> <span>Facebook</span>
            </Link>
          </div>
        </>
      )}
    </div>
  );
};

export default ChurchServices;
