import { ServicesListType } from "ServicesListType";
import { combineReducers } from "redux";
import { createReducer } from "typesafe-actions";
import { fetchServicesListAsync } from "store/actions/servicesList";

export const isFetching = createReducer(false as boolean)
  .handleAction([fetchServicesListAsync.request], (_state, _action) => true)
  .handleAction(
    [fetchServicesListAsync.success, fetchServicesListAsync.failure],
    (_state, _action) => false
  );

export const payload = createReducer([] as Array<ServicesListType>).handleAction(
  fetchServicesListAsync.success,
  (_state, action) => action.payload
);

const servicesListReducer = combineReducers({
  isFetching,
  payload,
});

export default servicesListReducer;
export type ServicesListState = ReturnType<typeof servicesListReducer>;
