import { HomeDetailsType } from "HomeDetailsType";
import { combineReducers } from "redux";
import { createReducer } from "typesafe-actions";
import { fetchHomeDetailsAsync } from "store/actions/homeDetails";

export const isFetching = createReducer(false as boolean)
  .handleAction([fetchHomeDetailsAsync.request], (_state, _action) => true)
  .handleAction(
    [fetchHomeDetailsAsync.success, fetchHomeDetailsAsync.failure],
    (_state, _action) => false
  );

export const payload = createReducer({} as HomeDetailsType).handleAction(
  fetchHomeDetailsAsync.success,
  (_state, action) => action.payload
);

const homeDetailsReducer = combineReducers({
  isFetching,
  payload,
});

export default homeDetailsReducer;
export type HomeDetailsState = ReturnType<typeof homeDetailsReducer>;
