import { Epic } from "redux-observable";
import { from, of } from "rxjs";
import { filter, mergeMap, map, catchError } from "rxjs/operators";
import { RootAction, RootState, Services, isActionOf } from "typesafe-actions";

import { fetchLifeGroupListAsync } from "store/actions/lifeGroupList";

export const fetchLifeGroupListAsyncEpic: Epic<RootAction, RootAction, RootState, Services> = (
  action$,
  _state$,
  { api }
) =>
  action$.pipe(
    filter(isActionOf(fetchLifeGroupListAsync.request)),
    mergeMap(() =>
      from(api.lifeGroupList.fetchLifeGroupList()).pipe(
        map(fetchLifeGroupListAsync.success),
        catchError((message: string) => of(fetchLifeGroupListAsync.failure(message)))
      )
    )
  );
