import { EventDetailsType, EventDetailsTypeI18n } from "EventDetailsType";
import { FirebaseDocumentType } from "FirebaseDataType";
import { firestoreParser } from "helpers/functions";

export function fetchEventDetails(eventId: string): Promise<EventDetailsType> {
  const lang = sessionStorage.getItem("lang");

  return new Promise((resolve, reject) => {
    fetch(
      `https://firestore.googleapis.com/v1/projects/tlcfm-japan/databases/(default)/documents/eventsList/${eventId}`
    )
      .then(async (res) => {
        const data: FirebaseDocumentType = await res.json();
        const parsedData: EventDetailsTypeI18n = firestoreParser(data)
          .fields as EventDetailsTypeI18n;
        const newData = {
          ...parsedData,
          id: eventId,
          name: parsedData.name[lang as "en" | "ja"],
          location: {
            address: parsedData.location.address[lang as "en" | "ja"],
            name: parsedData.location.name[lang as "en" | "ja"],
            googleMapLink: parsedData.location.googleMapLink,
          },
          details: parsedData.details && parsedData.details[lang as "en" | "ja"],
        };

        resolve(newData);
      })
      .catch((data) => reject(data));
  });
}
