import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import styles from "./App.module.scss";
import { Button, Menu, MenuItem } from "@material-ui/core";
import { Public, ArrowDropDown } from "@material-ui/icons";
import { language } from "constants/common";
import { classNames } from "helpers/functions";
import { fetchHomeDetailsAsync } from "store/actions/homeDetails";
import { homeDetails } from "store/selectors/homeDetails";
import Header from "_root/Header";
import Footer from "_root/Footer";

const App: React.FC = (props) => {
  const { i18n } = useTranslation();
  const [selectedI18N, setSelectedI18N] = useState(0);
  const dispatch = useDispatch();
  const { payload } = useSelector(homeDetails);
  const location = useLocation();
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);

  useEffect(() => {
    sessionStorage.setItem("lang", i18n.language);
  });

  useEffect(() => {
    if (!Object.keys(payload).length) dispatch(fetchHomeDetailsAsync.request());
  }, [dispatch, payload]);

  const handleClickListItem = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuItemClick = (_event: React.MouseEvent<HTMLElement>, index: number) => {
    i18n.changeLanguage(language[index].lang);
    sessionStorage.setItem("lang", language[index].lang);
    setTimeout(() => {
      setSelectedI18N(index);
      setAnchorEl(null);
    }, 500);
  };

  const handleCloseMenu = () => setAnchorEl(null);

  let bgInlineStyle = {};
  let hasBGShadow = false;

  if (location.pathname === "/events") {
    bgInlineStyle = {
      background: `url(${payload.eventsPageBG}) center center / cover no-repeat fixed`,
    };
    hasBGShadow = true;
  } else if (location.pathname === "/lifeGroups") {
    bgInlineStyle = {
      background: `url(${payload.lifeGroupsBG}) center center / cover no-repeat fixed`,
    };
    hasBGShadow = false;
  } else if (location.pathname.includes("/ministryDetails")) {
    bgInlineStyle = {
      background: `url(${payload.ministriesBG}) center center / cover no-repeat fixed`,
    };
    hasBGShadow = true;
  }

  return (
    <div className={styles.wrapper} style={bgInlineStyle}>
      <div className={classNames(styles.container, hasBGShadow && styles.hasBGShadow)}>
        <Header />
        <Button
          className={styles.i18n}
          startIcon={<Public />}
          endIcon={<ArrowDropDown />}
          onClick={handleClickListItem}
        >
          {language[selectedI18N].lang}
        </Button>
        <div className={styles.children}>{props.children}</div>
        <Footer />

        <Menu
          id="lock-menu"
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={handleCloseMenu}
        >
          {language.map((item, idx) => (
            <MenuItem
              key={item.lang}
              selected={idx === selectedI18N}
              onClick={(event) => handleMenuItemClick(event, idx)}
            >
              <img
                className={styles.langIcon}
                src={process.env.PUBLIC_URL + item.icon}
                alt={language[selectedI18N].lang}
              />
              {item.text}
            </MenuItem>
          ))}
        </Menu>
      </div>
    </div>
  );
};

export default App;
