import React from "react";
import { useTranslation } from "react-i18next";
import VisibilitySensor from "react-visibility-sensor";
// UI FRAMEWORK
import { Link } from "@material-ui/core";
// TYPES
import { LifeGroupListType } from "LifeGroupListType";
// HELPERS
import { classNames } from "helpers/functions";
// STYLES
import styles from "./LifeGroupDetails.module.scss";

type Props = {
  details: LifeGroupListType;
  aosDirection: string;
};

const LifeGroupDetails: React.FC<Props> = ({ details, aosDirection }) => {
  const { t } = useTranslation();
  return (
    <VisibilitySensor
      partialVisibility={"bottom" as any}
      offset={{
        bottom: -350,
      }}
    >
      {({ isVisible }) => {
        let aosClassName = "";
        if (isVisible)
          aosClassName = aosDirection === "left" ? styles.scrolledLeft : styles.scrolledRight;
        else
          aosClassName = aosDirection === "left" ? styles.scrolledLeftOut : styles.scrolledRightOut;

        return (
          <div className={classNames(styles.wrapper, aosClassName)}>
            <img className={styles.lgBanner} src={details.banner} alt={details.name} />
            <div className={styles.lgDetails}>
              <span className={styles.title}>{details.name}</span>
              <span>{`${t("lifeGroupDetails.contactPerson")}: ${details.contactPerson}`}</span>
              <span>{`${t("lifeGroupDetails.contactNo")}: ${details.contactNumber}`}</span>
              <span>{`${t("lifeGroupDetails.location")}: ${details.location}`}</span>
              <span>{`${t("lifeGroupDetails.stationNearby")}: ${details.stationNearBy}`}</span>
              <Link
                className={styles.direction}
                href={details.googleMapLink}
                target={"_blank"}
                rel={"noopener"}
              >
                {t("lifeGroupDetails.direction")}
              </Link>
            </div>
          </div>
        );
      }}
    </VisibilitySensor>
  );
};

export default LifeGroupDetails;
