import { LifeGroupListType } from "LifeGroupListType";
import { combineReducers } from "redux";
import { createReducer } from "typesafe-actions";
import { fetchLifeGroupListAsync } from "store/actions/lifeGroupList";

export const isFetching = createReducer(false as boolean)
  .handleAction([fetchLifeGroupListAsync.request], (_state, _action) => true)
  .handleAction(
    [fetchLifeGroupListAsync.success, fetchLifeGroupListAsync.failure],
    (_state, _action) => false
  );

export const payload = createReducer([] as Array<LifeGroupListType>).handleAction(
  fetchLifeGroupListAsync.success,
  (_state, action) => action.payload
);

const lifeGroupListReducer = combineReducers({
  isFetching,
  payload,
});

export default lifeGroupListReducer;
export type LifeGroupListState = ReturnType<typeof lifeGroupListReducer>;
