import React from "react";
// ROUTER
import { useHistory } from "react-router-dom";
// UI FRAMEWORK
import { LocationOn } from "@material-ui/icons";
// TYPE
import { EventListType } from "EventListType";
// HELPERS
import { classNames, getDateFormat } from "helpers/functions";
// STYLES
import styles from "./EventSummary.module.scss";

type Props = {
  details: EventListType;
  isPastEvents?: boolean;
};

const EventSummary: React.FC<Props> = ({ details, isPastEvents = false }) => {
  const history = useHistory();

  const handleOnClickItem = () =>
    history.push({
      pathname: `/events/${details.id}`,
      state: {
        isPastEvents,
      },
    });

  return (
    <div className={styles.wrapper} onClick={handleOnClickItem}>
      <img className={styles.eventBanner} src={details.bannerImg} alt={details.location.name} />
      <div className={styles.eventTitleWrapper}>
        <div className={classNames(styles.title, styles.ellipsis)}>{details.name}</div>
        <div className={styles.ellipsis}>
          {details.date.from ? getDateFormat(details.date) : "To be announced"}
        </div>
      </div>
      <div className={styles.eventLocation}>
        <LocationOn />
        <span className={styles.ellipsis}>{details.location.name}</span>
      </div>
    </div>
  );
};

export default EventSummary;
