import { combineReducers } from "redux";
import eventDetailsReducer from "store/reducers/eventDetails";
import eventListReducer from "store/reducers/eventList";
import servicesListReducer from "store/reducers/servicesList";
import homeDetailsReducer from "store/reducers/homeDetails";
import lifeGroupListReducer from "store/reducers/lifeGroupList";
import ministriesReducer from "store/reducers/ministries";

const rootReducer = combineReducers({
  eventDetails: eventDetailsReducer,
  eventList: eventListReducer,
  servicesList: servicesListReducer,
  homeDetails: homeDetailsReducer,
  lifeGroupList: lifeGroupListReducer,
  ministries: ministriesReducer,
});

export default rootReducer;
