import { EventDetailsEpicParamsType } from "EventDetailsType";
import { Epic } from "redux-observable";
import { from, of } from "rxjs";
import { filter, mergeMap, map, catchError } from "rxjs/operators";
import { RootAction, RootState, Services, isActionOf } from "typesafe-actions";

import { fetchEventDetailsAsync } from "store/actions/eventDetails";

export const fetchEventDetailsAsyncEpic: Epic<RootAction, RootAction, RootState, Services> = (
  action$,
  _state$,
  { api }
) =>
  action$.pipe(
    filter(isActionOf(fetchEventDetailsAsync.request)),
    mergeMap((params: EventDetailsEpicParamsType) =>
      from(api.eventDetails.fetchEventDetails(params.payload)).pipe(
        map(fetchEventDetailsAsync.success),
        catchError((message: string) => of(fetchEventDetailsAsync.failure(message)))
      )
    )
  );
