import React from "react";
import VisibilitySensor from "react-visibility-sensor";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
// UI FRAMEWORK
import { Link } from "@material-ui/core";
// CONSTANTS
import { globalLink } from "constants/common";
// HELPERS
import { classNames } from "helpers/functions";
// REDUX
import { homeDetails } from "store/selectors/homeDetails";
// STYLES
import styles from "./ChurchLocation.module.scss";

export const ChurchLocation: React.FC = () => {
  const { payload } = useSelector(homeDetails);

  const { t } = useTranslation();
  return (
    <div
      id="churchLocation"
      className={styles.wrapper}
      style={{
        background: `url(${payload.locationBG}) center center / cover no-repeat`,
      }}
    >
      <div className={styles.container}>
        <p className={styles.sectionTitle}>{t("location.title")}</p>
        <VisibilitySensor partialVisibility={"bottom" as any} offset={{ bottom: -200 }}>
          {({ isVisible }) => (
            <>
              <img
                className={classNames(isVisible && styles.scrolledUp, styles.mapIcon)}
                src={process.env.PUBLIC_URL + "/images/mapIcon.png"}
                alt={"LocationMap"}
              />
              <div className={classNames(isVisible && styles.scrolledUp, styles.addressContainer)}>
                {t("location.address")}
              </div>
              <Link
                className={styles.viewDirection}
                href={globalLink.googlaMapLink}
                target={"_blank"}
                rel={"noopener"}
              >
                {t("location.viewChurchDirection")}
              </Link>
            </>
          )}
        </VisibilitySensor>
      </div>
    </div>
  );
};

export default ChurchLocation;
