import { Epic } from "redux-observable";
import { from, of } from "rxjs";
import { filter, mergeMap, map, catchError } from "rxjs/operators";
import { RootAction, RootState, Services, isActionOf } from "typesafe-actions";

import { fetchHomeDetailsAsync } from "store/actions/homeDetails";

export const fetchHomeDetailsAsyncEpic: Epic<RootAction, RootAction, RootState, Services> = (
  action$,
  _state$,
  { api }
) =>
  action$.pipe(
    filter(isActionOf(fetchHomeDetailsAsync.request)),
    mergeMap(() =>
      from(api.homeDetails.fetchHomeDetails()).pipe(
        map(fetchHomeDetailsAsync.success),
        catchError((message: string) => of(fetchHomeDetailsAsync.failure(message)))
      )
    )
  );
