import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
// COMPONENT
import { Spinner } from "components";
// CHILDREN COMPONENTS
import LifeGroupDetails from "./components/LifeGroupDetails";
// REDUX
import { lifeGroupList } from "store/selectors/lifeGroupList";
import { fetchLifeGroupListAsync } from "store/actions/lifeGroupList";
// STYLES
import styles from "./LifeGroups.module.scss";

const LifeGroups: React.FC = () => {
  const dispatch = useDispatch();
  const { i18n } = useTranslation();
  const { payload, isFetching } = useSelector(lifeGroupList);

  useEffect(() => {
    window.scroll({
      top: 0,
      left: 0,
      behavior: "smooth",
    });
    dispatch(fetchLifeGroupListAsync.request());
  }, [dispatch, i18n.language]);

  return (
    <div className={styles.wrapper}>
      {isFetching ? (
        <Spinner />
      ) : (
        <div className={styles.container}>
          {payload.length &&
            payload.map((item, index) => {
              const direction = index % 2 === 0 ? "left" : "right";
              return <LifeGroupDetails key={item.id} aosDirection={direction} details={item} />;
            })}
        </div>
      )}
    </div>
  );
};

export default LifeGroups;
