import { LifeGroupListType, LifeGroupListTypeI18n } from "LifeGroupListType";
import { FirebaseDocumentType } from "FirebaseDataType";
import { firestoreParser } from "helpers/functions";
import { firebaseNameDirectory } from "constants/common";

export function fetchLifeGroupList(): Promise<Array<LifeGroupListType>> {
  const lang = sessionStorage.getItem("lang");

  return new Promise((resolve, reject) => {
    fetch(
      `https://firestore.googleapis.com/v1/projects/tlcfm-japan/databases/(default)/documents/lifeGroupList`
    )
      .then(async (res) => {
        const data: FirebaseDocumentType = await res.json();
        const parsedData = firestoreParser(data).documents.map((item: FirebaseDocumentType) => {
          const fields: LifeGroupListTypeI18n = item.fields as LifeGroupListTypeI18n;
          return {
            ...fields,
            id: item.name.replace(firebaseNameDirectory + "lifeGroupList/", ""),
            contactPerson: fields.contactPerson[lang as "en" | "ja"],
            name: fields.name[lang as "en" | "ja"],
            location: fields.location[lang as "en" | "ja"],
            stationNearBy: fields.stationNearBy[lang as "en" | "ja"],
          };
        });

        resolve(parsedData);
      })
      .catch((data) => reject(data));
  });
}
