import { EventListType, EventCategoryType, EventListTypeI18n } from "EventListType";
import { FirebaseDataType, FirebaseDocumentType } from "FirebaseDataType";
import dayjs from "dayjs";
import { firestoreParser } from "helpers/functions";
import { firebaseNameDirectory } from "constants/common";

export function fetchEventList(): Promise<EventCategoryType> {
  const lang = sessionStorage.getItem("lang");

  return new Promise((resolve, reject) => {
    fetch(
      "https://firestore.googleapis.com/v1/projects/tlcfm-japan/databases/(default)/documents/eventsList"
    )
      .then(async (res) => {
        const data: FirebaseDataType = await res.json();
        const parsedData = firestoreParser(data).documents.map((item: FirebaseDocumentType) => {
          const fields: EventListTypeI18n = item.fields as EventListTypeI18n;
          return {
            ...fields,
            id: item.name.replace(firebaseNameDirectory + "eventsList/", ""),
            name: fields.name[lang as "en" | "ja"],
            location: {
              address: fields.location.address[lang as "en" | "ja"],
              name: fields.location.name[lang as "en" | "ja"],
              googleMapLink: fields.location.googleMapLink,
            },
          };
        });
        const sortedPastEvents = [...parsedData].sort((a: any, b: any) => {
          a = a.date.from.split("/");
          b = b.date.from.split("/");
          return b[2] - a[2] || b[0] - a[0] || b[1] - a[1];
        });

        const sortedUpcomingEvents = [...parsedData].sort((a: any, b: any) => {
          a = a.date.from.split("/");
          b = b.date.from.split("/");
          return a[2] - b[2] || a[0] - b[0] || a[1] - b[1];
        });

        const upcomingEvents = sortedUpcomingEvents.filter(
          (item: EventListType) =>
            !dayjs(item.date.from).isBefore(dayjs(new Date()).format("MM/DD/YYYY"))
        );

        const pastEvents = sortedPastEvents.filter((item: EventListType) =>
          dayjs(item.date.from).isBefore(dayjs(new Date()).format("MM/DD/YYYY"))
        );

        const newData: EventCategoryType = { upcomingEvents, pastEvents };
        resolve(newData);
      })
      .catch((data) => reject(data));
  });
}
