import { Epic } from "redux-observable";
import { from, of } from "rxjs";
import { filter, mergeMap, map, catchError } from "rxjs/operators";
import { RootAction, RootState, Services, isActionOf } from "typesafe-actions";

import { fetchServicesListAsync } from "store/actions/servicesList";

export const fetchServicesListAsyncEpic: Epic<RootAction, RootAction, RootState, Services> = (
  action$,
  _state$,
  { api }
) =>
  action$.pipe(
    filter(isActionOf(fetchServicesListAsync.request)),
    mergeMap(() =>
      from(api.servicesList.fetchServicesList()).pipe(
        map(fetchServicesListAsync.success),
        catchError((message: string) => of(fetchServicesListAsync.failure(message)))
      )
    )
  );
