import React, { useState, useEffect } from "react";
import { useHistory, useLocation } from "react-router-dom";
import {
  AppBar,
  IconButton,
  Drawer,
  Toolbar,
  useScrollTrigger,
  Typography,
} from "@material-ui/core";
import { Menu as MenuIcon } from "@material-ui/icons";
import { useDeviceType } from "helpers/custom hooks";
import classNames from "helpers/functions/classNames";
import HeaderLinks from "./HeaderLinks";
import styles from "./Header.module.scss";

const MenuHeader: React.FC = () => {
  const [openMenuDrawer, setOpenMenuDrawer] = useState(false);
  const deviceType = useDeviceType();
  const history = useHistory();
  const location = useLocation();
  const isStickyHeader = location.pathname.includes("/events");

  useEffect(() => {
    const handleResize = () => setOpenMenuDrawer(false);
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  const handleOnClickLogo = () => history.push("/");

  const trigger = useScrollTrigger({ target: window, disableHysteresis: true, threshold: 100 });

  const headerStyles = isStickyHeader
    ? styles.fixedStyleHeader
    : trigger
    ? styles.scrolledHeader
    : styles.topHeader;

  return (
    <AppBar className={classNames(styles.wrapper, headerStyles)}>
      <Toolbar className={styles.toolBarContainer}>
        <div className={styles.logoContainer} onClick={handleOnClickLogo}>
          <svg width={"100px"} height={"65px"}>
            <path
              id="path1"
              d="M 6 4 V 57 C 35 51.3333 64 45.6667 93 40 C 93 33.6667 93 27.3333 93 21"
              fill={"#009544"}
            />
          </svg>
          <Typography variant="h6">TLC-FM</Typography>
        </div>
        <div className={styles.HeaderLinkContainer}>
          {deviceType !== "pc" ? (
            <IconButton
              edge="start"
              color="inherit"
              aria-label="menu"
              onClick={() => setOpenMenuDrawer(true)}
            >
              <MenuIcon
                style={{
                  fontSize: 35,
                }}
              />
            </IconButton>
          ) : (
            <HeaderLinks isScrolled={trigger} />
          )}
        </div>
        <Drawer
          classes={{
            paper: styles.drawerPaper,
          }}
          anchor={"right"}
          open={openMenuDrawer && deviceType !== "pc"}
          onClose={() => setOpenMenuDrawer(false)}
        >
          <Toolbar />
          <HeaderLinks onClick={() => setOpenMenuDrawer(false)} />
        </Drawer>
      </Toolbar>
    </AppBar>
  );
};

export default MenuHeader;
